"use client"

import { useEffect, useState } from "react"
import VideoCard from "@/components/video-card"
import { Input } from "@/components/ui/input"
import { Search, Clapperboard } from "lucide-react" // Import icons

interface Video {
    _id: string
    title: string
    description: string
    thumbnail: string
    views: number
}

export default function VideosPage() {
    const [videos, setVideos] = useState<Video[]>([])
    const [filteredVideos, setFilteredVideos] = useState<Video[]>([])
    const [search, setSearch] = useState("")
    const [loading, setLoading] = useState(true)

    // --- Data Fetch (Functional Logic Unchanged) ---
    useEffect(() => {
        async function fetchVideos() {
            try {
                const res = await fetch("/api/videos")
                const data = await res.json()
                setVideos(data)
                setFilteredVideos(data)
            } catch (error) {
                console.error("Failed to fetch videos:", error)
            } finally {
                setLoading(false)
            }
        }
        fetchVideos()
    }, [])

    // --- Search/Filter (Functional Logic Unchanged) ---
    useEffect(() => {
        const filtered = videos.filter(
            (video) =>
                video.title.toLowerCase().includes(search.toLowerCase()) ||
                video.description.toLowerCase().includes(search.toLowerCase()),
        )
        setFilteredVideos(filtered)
    }, [search, videos])

    return (
        // Aesthetic Improvement: Softer background and more padding
        <main className="min-h-screen bg-gray-50 dark:bg-gray-900 py-16 px-4 sm:px-6 lg:px-8">
            <div className="max-w-7xl mx-auto">
                
                {/* Aesthetic Improvement: Enhanced Title with Icon */}
                <h1 className="text-5xl font-extrabold text-foreground mb-4 flex items-center">
                    <Clapperboard className="w-8 h-8 mr-3 text-primary" />
                    Video Library
                </h1>
                <p className="text-xl text-muted-foreground mb-10">Explore our curated collection of resources.</p>

                {/* Search Bar - Aesthetic Improvement: Added Icon and full-width container */}
                <div className="mb-12 relative max-w-xl mx-auto">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />
                    <Input
                        placeholder="Search videos by title or description..."
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                        className="w-full pl-10 py-6 text-base shadow-lg"
                    />
                </div>

                {/* Conditional Loading States */}
                {loading ? (
                    // Aesthetic Improvement: Better loading state
                    <div className="flex items-center justify-center py-24">
                        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
                        <p className="ml-4 text-xl text-primary">Loading Videos...</p>
                    </div>
                ) : filteredVideos.length === 0 ? (
                    // Aesthetic Improvement: Better "No results" state
                    <div className="text-center py-24 border border-dashed rounded-xl bg-card/50">
                        <Clapperboard className="w-16 h-16 text-muted-foreground/50 mx-auto mb-4" />
                        <p className="text-2xl text-muted-foreground font-medium">
                            No videos found matching "{search}"
                        </p>
                    </div>
                ) : (
                    // Video Grid
                    <div className="grid sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
                        {filteredVideos.map((video) => (
                            // Subtly animate the cards in 
                            <div key={video._id} className="animate-fade-in-up"> 
                                <VideoCard id={video._id} {...video} />
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </main>
    )
}