"use client"

import Link from "next/link"
import { Card, CardContent } from "@/components/ui/card"
import { Eye, PlayCircle } from "lucide-react"

interface VideoCardProps {
    id: string
    title: string
    description: string
    thumbnail: string
    views: number
}

// Helper function to format views (e.g., 120000 -> 120K)
const formatViews = (views: number) => {
    if (views >= 1000000) {
        return (views / 1000000).toFixed(1) + "M";
    }
    if (views >= 1000) {
        return (views / 1000).toFixed(1) + "K";
    }
    return views.toString();
}

/**
 * Robustly determines the correct thumbnail URL.
 * Assumes if the input isn't a full URL, it's a YouTube Video ID.
 */
const getImageUrl = (url: string) => {
    // 1. Check if the input is a full URL
    if (url && (url.startsWith('http://') || url.startsWith('https://'))) {
        return url; // Use the provided full URL
    }
    // 2. Assume the input is a YouTube Video ID and construct the high-res URL
    if (url) {
        // This is a common, reliable high-resolution thumbnail pattern
        return `https://img.youtube.com/vi/${url}/maxresdefault.jpg`; 
    }
    // 3. Use the local fallback placeholder
    return "/placeholder-video.svg"; // Changed to a simpler, reliable local placeholder
}


export default function VideoCard({ id, title, description, thumbnail, views }: VideoCardProps) {
    
    const finalThumbnailUrl = getImageUrl(thumbnail);

    return (
        <Link href={`/videos/${id}`} className="block h-full">
            {/* Aesthetic Improvement: Enhanced shadow, border, and hover effect */}
            <Card className="cursor-pointer border-2 border-transparent hover:border-primary/50 hover:shadow-xl hover:-translate-y-1 transition duration-500 ease-in-out h-full overflow-hidden">
                <CardContent className="p-0">
                    
                    {/* Image Area with Play Icon Overlay */}
                    <div className="relative aspect-video bg-muted overflow-hidden rounded-t-xl group">
                        <img
                            src={finalThumbnailUrl} 
                            alt={title}
                            // Aesthetic Improvement: Subtle scale animation on hover
                            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500"
                            // Fix: Robust error handling for network failures
                            onError={(e) => {
                                e.currentTarget.src = "/placeholder-video.svg"; 
                                e.currentTarget.onerror = null;
                            }}
                        />
                        {/* Aesthetic Improvement: Enhanced Play button overlay */}
                        <div className="absolute inset-0 flex items-center justify-center bg-black/30 group-hover:bg-black/50 transition duration-300">
                            <PlayCircle className="w-16 h-16 text-white opacity-80 group-hover:opacity-100 group-hover:scale-110 transition duration-300" fill="currentColor"/>
                        </div>
                    </div>

                    <div className="p-4 flex flex-col justify-between h-auto">
                        {/* Aesthetic Improvement: Stronger title font */}
                        <h3 className="text-xl font-semibold text-foreground line-clamp-2 mb-2">{title}</h3>
                        <p className="text-sm text-gray-500 line-clamp-2">{description}</p>
                        
                        {/* Aesthetic Improvement: Views with icon and separator */}
                        <div className="flex items-center text-xs text-muted-foreground mt-3 pt-2 border-t border-border/70">
                            <Eye className="w-4 h-4 mr-1 text-primary/70" />
                            <span>{formatViews(views)} views</span>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </Link>
    )
}